(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension', ['ngRoute', 'ui.bootstrap']);

})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController() {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = vm.creationDate;
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController() {
    var vm = this;

    var genre = {
      'pop'  : [{id:1, image:'http://lorempixel.com/' + 601 + '/300', artist:'Michal Jackson', title:'Bad'},
                {id:2, image:'http://lorempixel.com/' + 602 + '/300', artist:'Sting', title:'Fields of Barley'}],
      'rock' : [{id:3, image:'http://lorempixel.com/' + 601 + '/300', artist:'Nickleback', title:'Rockstar'},
                {id:4, image:'http://lorempixel.com/' + 602 + '/300', artist:'Foo Fighters', title:'Breakout'}]
    };

    vm.genreKeys = Object.keys(genre);
    vm.creationDate = 1462648970556;
    vm.selectedGenre = genre['pop'];

    vm.setGenre = function(key) {
      vm.selectedGenre = genre[key];
    };

    // function addSlide() {
    //   var newWidth = 600 + slides.length + 1;
    //   slides.push({
    //     image: 'http://lorempixel.com/' + newWidth + '/300',
    //     text: ['Nice image','Awesome photograph','That is so cool','I love that'][slides.length % 4],
    //     id: currIndex++
    //   });
    // }

  }
})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .config(routeConfig);

  function routeConfig($routeProvider, $locationProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .otherwise({
        redirectTo: '/'
      });
    $locationProvider.html5Mode(true);
  }

})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension');

})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .config(config);

  /** @ngInject */
  function config($logProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib

  }

})();

angular.module("jukeboxcomprehension").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=container><div class=row><acme-navbar creation-date=main.creationDate></acme-navbar></div><div class=row><div><div style=\"height: 305px\"><uib-carousel active=0 no-wrap=false interval=0><uib-slide ng-repeat=\"song in main.selectedGenre track by song.id\"><img class=img-circle ng-src={{song.image}} style=margin:auto><div class=carousel-caption><p>{{song.artist}}</p><p>{{song.title}}</p></div></uib-slide></uib-carousel></div><div class=row><div ng-repeat=\"genreKey in main.genreKeys\" class=col-sm-6><div class=\"thumbnail text-center\"><h2><button class=\"btn btn-info\" ng-click=main.setGenre(genreKey)><i class=\"glyphicon glyphicon-music\"></i></button> {{genreKey | uppercase}}</h2></div></div></div></div></div></div>");
$templateCache.put("app/components/navbar/navbar.html","<nav class=\"navbar navbar-static-top navbar-inverse\"><div class=container-fluid><div class=navbar-header><a class=navbar-brand href=#><span class=\"glyphicon glyphicon-home\"></span> Gulp Angular</a></div><div class=\"collapse navbar-collapse\" id=bs-example-navbar-collapse-6><ul class=\"nav navbar-nav\"><li class=active><a ng-href=#>Home</a></li><li><a ng-href=#>About</a></li><li><a ng-href=#>Contact</a></li></ul><ul class=\"nav navbar-nav navbar-right acme-navbar-text\"><li>Application was created {{ vm.relativeDate | date:\'yyyy-MM-dd HH:mm:ss Z\' }}.</li></ul></div></div></nav>");}]);